@import 'footer';
@import 'navbar';
@import 'thankyou';
@import 'contact';
@import 'aboutme';
@import 'projects';


$first-color: #0C0032;						
$second-color: #464866;		
$third-color: #211A36;	
$contact-form: #edf5e1;
$main-font: Georgia, 'Times New Roman', Times, serif;
$hover:#659DBD;
$hover-menu:#FF0700;
$text-color:white;


* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
html, body {
  line-height: 1.5;
  font-size: 20px;
  font-family:$main-font;
  height: 100%;
  margin-left: 100px;
  padding: 0 20px;   
  background: $third-color;
}

section h1 {
  font-size: 40px;
  text-align: center;
  padding: 10px;
  color: $text-color;						
}

section p {
  color: $text-color;	
  text-align: justify;
  hyphens: auto;
  -webkit-hyphens: auto;
  -webkit-hyphenate-limit-before: 3;
  -webkit-hyphenate-limit-after: 3;
  -webkit-hyphenate-limit-chars: 6 3 3;
  -webkit-hyphenate-limit-lines: 2;
  -webkit-hyphenate-limit-last: always;
  -webkit-hyphenate-limit-zone: 8%;
}

.horizontal__line {
  border: 2px solid $hover-menu;
  border-radius: 100%;
  width: 30%;
  margin: auto;  
}

li {
    list-style: none;
}
  
a {
  text-decoration: none;
}

@media screen and (max-width: 700px) {
    
html, body {
  margin-left: 0;
  padding: 0;
  margin-top: 120px;
}
 
section h1 {
  font-size: 35px;
  padding: 0px;	
}

.horizontal__line {
  width: 70%;
  }
}

 

  





  
 
  
  
.aboutme {
  max-width: 1600px;
  padding: 20px 0;
  p {
    text-align:justify;
    padding: 5px 0;
    }
  .aboutme__photo {
	  margin: 0 40px 0 20px;
	  float: left;
  .aboutme__img {
    max-width: 300px;
    height: auto;
	  border-radius: 50%;
    filter: drop-shadow(1px 1px 20px $hover-menu);
  }
}
}
  
@media only screen and (max-width: 900px) {

.aboutme {
  display: flex;
  flex-direction: column-reverse;
  margin: 0 20px;
  padding: 10px 0;
  .aboutme__text p { 
    padding-bottom: 10px;
  }
  .aboutme__photo {
    margin: auto;
    padding-top: 20px;
  }
}
}

@media only screen and (max-width: 700px) {

.aboutme {
  margin: 0 25px;
  padding-bottom: 30px;
  .aboutme__text p { 
    text-align: left; 
  }
}
}


    

 









.nav__header {
  display:flex;
  flex-direction: column;
  justify-content: space-evenly;
  padding-bottom: 50px;
  h3, p { 
    color: #ffffff;
    text-align: center;
    padding: 3px;
  }
}

.nav__header__logo {
  height:5rem;
  margin: 50px auto 40px;
}

.nav {
  height: 100%;
  width: 200px;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  background-color: $first-color;	
  ul {
    background-color: $second-color;
    display: block;
    text-align: center;
    padding: 1px 0;
  li {
    padding: 5px 0;
    margin: 31.9px 0;
    a {
      font-weight: bold;
      color: #ffffff;
      font-size: 25px;  
      &:hover {
        color: $hover-menu;
      }
    }
  }
}
}
  
.drop__bar {
  display: none;
  .drop__bar__line1, .drop__bar__line2, .drop__bar__line3 {
    display: block;
    width: 25px;
    height: 4px;
    margin: 6px auto;
    background-color: white;
  }
}

.nav__socialmedia  { 
  text-align: center;
  a { 
    display: inline-block;
    border-radius: 2px;
    color: #ffffff;
    font-size: 50px; 
    justify-content: center;
    margin: 35px 5px;
    &:hover {
      transform:scale(1.1); 
      -webkit-transform:scale(1.1);
      color: $hover-menu;
    }
  }
}

@media screen and (max-width: 700px) {

.nav__header{
  padding-bottom: 10px;
  h3, p { 
  padding: 0px;
  }
}
  
.nav__header__logo {
  height:4rem;
  margin: 20px auto 0;
}
    
.nav {
  overflow: hidden;
  position: fixed;
  width: 100%;
  height: 210px;
  ul {
    position: fixed;
    left: -150%;
    top: 210px;
    z-index: 1;
    flex-direction: column;
    background-color: $second-color;
    width: 100%;
    border-bottom-right-radius: 10px;
    border-bottom-left-radius: 10px;
    text-align: center;
    transition: 0.5s;
    li {
      margin: 5px;
      a {
        font-size: 25px;
        color: #ffffff;
        &:hover {
          color: $hover-menu;           
        }
      }
    }
  } 
}
    
.drop__bar {
  display: block;
  cursor: pointer;
  position: absolute;
  left:90%;
  top:6%;
}

.drop__bar__line1, .drop__bar__line2, .drop__bar__line3 {
  transition: 0.4s;
}

.change .drop__bar__line1 {
  -webkit-transform: rotate(-45deg) translate(-9px, 9px) ;
  transform: rotate(-45deg) translate(-8px, 6px) ;
  height: 4px;
}
      
.change .drop__bar__line2 {
  opacity: 0;
  height: 4px;
}
      
.change .drop__bar__line3 {
  -webkit-transform: rotate(45deg) translate(-9px, -9px) ;
  transform: rotate(45deg) translate(-8px, -6px) ;
  height: 4px;
}

.nav__menu.active {
  left: 0;
}

.nav__socialmedia { 
  display:none; 
}
}
  


   






.contact {
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding: 20px 0 40px;
}

.contact__form {
  width: 37%;
  font-size: 22px;
  display: block;
}

input[type=text], textarea {
  display: flex;
  flex-direction: column;
  background-color: $hover;
  color: $text-color;
  width: 100%;
  padding: 12px;
  border-radius: 15px;
  margin-top: 6px;
  margin-bottom: 16px;
  font-size: 22px;
  font-family: $main-font;
  outline:none;
  resize: vertical;
}

input[type=text]:hover, textarea:hover {
  border: 2px solid $hover-menu;
}

input[type=submit] {
  background-color: $hover-menu;
  color: $text-color;
  padding: 10px 10px;
  border: none;
  border-radius: 15px;
  cursor: pointer;
  font-weight: bold;
  font-size: 20px;
  font-family: $main-font;
  width: 30%;
  position: relative;
  left: 35%;
  text-align: center;
}

input[type=submit]:hover {
  background-color: lightcoral;
  filter: drop-shadow(1px 1px 10px red);
}

form > label {
  font-weight: bold;
  color: $text-color;
}

.contact__vertical__line {
  border: 2px solid $text-color;	;
  height: 500px;
  margin: 80px 100px 0 100px;
  border-radius: 100%;
}

.contact__horizontal__line {
  display: none;
}

.contact__text {
  padding-top: 5px;
  display: grid;
  columns: 1;
  row-gap: 22px;
  text-align: center;
  font-size: 22px;
      p {
        text-align: center;
        font-family: 'Times New Roman', Times, serif;
      }
    }

.contact__text__phone__img1, .contact__text__email__img2 {
 font-size: 80px;
 color: $hover;
}

.icon-default {
  transition: opacity .3s, transform .3s;
}

.contact__text__phone i:hover, .contact__text__email i:hover {
  transform: scale(1.2); 
}

.contact__text__phone p {
    padding-top: 10px;
  }

.contact__text__email p {
    padding-bottom: 10px;
  }
  
.contact__text__address {
  p:nth-child(2) {
    padding-top: 10px;
  }
}   

@media only screen and (max-width: 1100px) {
  .contact {
    flex-direction: column;
    padding: 10px 0 40px;
  }

  .contact__form {
    width: 90%;
    margin: auto;
    padding-bottom: 20px;
  }

  input[type=submit] {
    font-size: 18px;
  }
  
  .contact__vertical__line {
    display: none;
  }

  .contact__horizontal__line {
    display: inline-block;
    border: 2px solid white;
    border-radius: 100%;
    width: 70%;
    margin: 20px auto;
  }

  .contact__text {
    padding-top: 20px;
    row-gap: 30px;
    margin-bottom:-20px;
    }
 .contact__text__address {
  p:nth-child(2) {
    padding-top: 20px;
  } 
}
}





























.projects {
  padding: 20px 0 20px;
  margin: auto;
}

.projects__text {
  text-align: center justify;
  width: 80%;
  margin: auto;
  padding-bottom:30px;
}

.projects__portfolio {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    row-gap: 30px;
    column-gap: 60px;
    }

.projects__portfolio__item {
  background-color: $second-color;
  border-bottom-style: solid;
  border-bottom-color: $hover-menu;
  border-radius: 10px;
  h3 {
    text-align: center;
    padding-top: 10px;
    color: $text-color;
  }
  p {
    padding: 10px 20px;
    text-align: left;
  }
  a {
    color:$text-color;
    cursor: pointer;
    font-weight: bold;
    font-size: 40px;
      &:hover {
        transform:scale(1.4); 
        -webkit-transform:scale(1.5);
         color: $hover-menu;
      }
    }
  }

.flipbox {
  max-width: 200px;
  height: 200px;
  cursor: pointer;
  margin: auto;
}
    
.flipbox__inner {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 0.8s;
  transform-style: preserve-3d;
}
    
.flipbox:hover .flipbox__inner {
  transform: rotateY(180deg);
}
    
.flipbox__front, .flipbox__front1, .flipbox__front2, .flipbox__back {
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 10px;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}
    
.flipbox__front {
  background-color: #d6c6b4;
  z-index: 1; //so pointer work properly on the back side
}

.flipbox__front1 {
  background-color:#116466;
  z-index: 1; //so pointer work properly on the back side
}

.flipbox__front2 {
  background-color:$text-color;
  z-index: 1; //so pointer work properly on the back side
}
    
.flipbox__back {
  background-color: $hover;
  transform: rotateY(180deg);
  z-index: 2; //so pointer work properly on the back side
}

.flip__img1,  .flip__img2 {
  max-width: 70%;
  position: relative;
  top: 15%;
}
    
.animated img {
  animation-duration: 3s;
  animation-iteration-count: infinite;
}
    
.pulse img {
  animation-name: pulse;
  animation-duration: 3s;
}

@keyframes pulse {
  0% {transform: scale(1);}
  50% {transform: scale(1.3);}
  100% {transform: scale(1);}
}

#myImg1 {
  border-radius: 5px;
  transition: 0.3s;
}

.myImg1 {
  max-width: 90%;
  position: relative;
  top: 9%;
}

.myImg2 {
  max-width: 90%;
  position: relative;
  top: 14%;
}

.modal {
  display: none; 
  position: fixed; 
  z-index: 1;
  padding-top: 50px;
  left: 0;
  top: 0;
  width: 100%; 
  height: 100%; 
  overflow: auto; /* Enable to scroll */
  background-color: rgba(0,0,0,0.9); /* Black + opacity */
}
    
.modal__content {
  margin: auto;
  display: block;
  width: 100%;
  max-width: 950px;
}
    
#caption {
  margin: auto;
  text-align: center;
  color: $text-color;
  padding-top: 10px;
}
    
.modal__content, #caption {  
  -webkit-animation-name: zoom;
  -webkit-animation-duration: 0.6s;
  animation-name: zoom;
  animation-duration: 0.6s;
}
    
@-webkit-keyframes zoom {
  from {-webkit-transform:scale(0)} 
  to {-webkit-transform:scale(1)}
}
    
@keyframes zoom {
  from {transform:scale(0)} 
  to {transform:scale(1)}
}
    
.close {
  position: absolute;
  top: 15px;
  right: 35px;
  color: $text-color;
  font-size: 60px;
  font-weight: bold;
  transition: 0.3s;
  &:hover, &:focus {
    color: $hover-menu;
    text-decoration: none;
    cursor: pointer;
  }
}


.projects__skills {
  display: grid;
  grid-template-columns: repeat(4, 1fr);  /* same sized column */
  column-gap: 30px;
  width: 80%;
  margin: auto;
  padding-bottom: 90px;
}

.projects__skills__text1, .projects__skills__img1 {
  grid-column: 1;
  grid-row: 1;
}

.projects__skills__text2, .projects__skills__img2 {
  grid-column: 2;
  grid-row: 1;
}
.projects__skills__text3, .projects__skills__img3 {
  grid-column: 3;
  grid-row: 1;
  }
.projects__skills__text4, .projects__skills__img4 {
  grid-column: 4;
  grid-row: 1;
  }

.projects__skills__text1, .projects__skills__text2, .projects__skills__text3, .projects__skills__text4 {
  border-radius: 5px;
  background-color: $hover;
  border-bottom-style: solid;
  border-bottom-color: $hover-menu;
  height: 70%;
  p {
    text-align: center;
    font-family: 'Times New Roman', Times, serif;
    font-size: 25px;
    color: $text-color;
    font-weight: bold;
    padding-top:5px;
  }
}

.projects__skills__img1, .projects__skills__img2 {
  max-width: 60px;
  height: auto;
  margin: auto;
  position: relative;
  top:90%;
}

.projects__skills__img3 {
  max-width: 65px;
  height: auto;
  margin: auto;
  position: relative;
  top:90%;
}

.projects__skills__img4 {
  max-width: 75px;
  height: auto;
  margin: auto;
  position: relative;
  top:90%;
}


@media only screen and (max-width: 1560px) {

.projects__portfolio {
  grid-template-columns: 1fr 1fr;
}

.modal__content {
  width: 80%;
}

.close {
  font-size: 50px;
  }
}
  
@media only screen and (max-width: 1080px) {

.projects__text {
    width: 100%;
}

.projects__skills {
