  }

.projects__skills__text1, .projects__skills__text2, .projects__skills__text3, .projects__skills__text4 {
    p {
    padding-bottom:20px;
  }
}

.projects__portfolio {
  grid-template-columns: 1fr;
  margin: auto 100px;
  }

.modal {
  padding-top: 100px;
}

.modal__content {
  width: 85%;
} 

.close {
  bottom: 100%;
