  }
}

@media only screen and (max-width: 900px) {

.projects__text{
  margin: auto 20px;
  width: auto;
}

.projects__portfolio {
  margin: auto 20px;
  p {
    text-align: left;    
  }
}

.modal__content {
  width: 90%;
  }
}

@media only screen and (max-width: 700px) {
.projects {
  padding: 10px 0 30px;
  margin: auto;
}

.projects__text {
