.projects__skills__img3 {
  max-width: 65px;
  height: auto;
  margin: auto;
  position: relative;
  top:93%;
}
  
.projects__portfolio {
  margin: auto 50px;
  p {
    text-align: left;    
  }
}

.projects__portfolio__item {
  h3 {
    font-size: 20px;
    }
  }

.modal__content {
  width: 90%;
  }
}

@media only screen and (max-width: 500px) {

.projects__portfolio {
  margin: auto 25px;
  }
