.home {
  background: url(/img/background.webp) center center no-repeat;		
  background-attachment: fixed;
  background-size: cover;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -moz-background-size: cover;
  margin: -17px -40px 20px;
  z-index: -100;
  height: auto;
  width: auto;
  min-width: 100%;
  min-height: 100%;
  color: $text-color;
  font-weight: bold;
  display: block;
}

.home__name {
  color: $text-color;
  font-weight: bold;
  display: block;
  li:nth-child(1) {   
    padding: 30px 50px;
    text-align: right;
    a {
      color: $text-color;
      font-weight: bold;
      font-family:Georgia, 'Times New Roman', Times, serif
    }
    button {
      padding:10px;
      background-color: $hover-menu;
      border-radius: 10px;
      &:hover {
        background-color: lightcoral;
        filter: drop-shadow(1px 1px 10px red);
      }
    }
  }

  li:nth-child(2) {
      overflow: hidden;
      white-space: nowrap;
      letter-spacing: 5px; 
      animation: typing 5s steps(100, end) both;
      font-size: 60px;
      margin: -30px auto auto;
      text-align: center;
    a {
      color: $text-color;
      font-size: 60px;
      font-weight: bold;
      letter-spacing: 5px; 
      &:hover {
        color: lightcoral;
        filter: drop-shadow(1px 1px 10px red);
      }
    }
  }

  li:nth-child(3) {
    overflow: hidden;
    white-space: nowrap;
    letter-spacing: 5px; 
    animation: typing 5s steps(100, end) both;
    animation-delay: 2s;
    font-size: 40px;
    margin: -10px auto auto;
    text-align: center;
  }  

  li:nth-child(4) {
    overflow: hidden;
    white-space: nowrap;
    letter-spacing: 5px; 
    animation: typing 5s steps(100, end) both;
    animation-delay: 4s;
    font-size: 30px;
    padding: 30.5px 0 65px;
    margin: auto;
    text-align: center;
    font-style: italic;     
  }  

  li:nth-child(5) {   
    visibility: hidden;
    margin-top: -30px;
  }

@keyframes typing {
  from { width: 0 }
  to { width: 100% }
}
}

@media only screen and (max-width: 1400px) {

.home__name {
  li:nth-child(1) {   
    visibility: hidden;
    margin-top: -86px;
  }

  li:nth-child(2) {
    animation: typing 3s steps(100, end) both;
    margin: 10px auto auto;
  }

  li:nth-child(3) {
    animation: typing 3s steps(100, end) both;
    animation-delay: 2s;
  }  

  li:nth-child(4) {
    animation: typing 3s steps(100, end) both;
    animation-delay: 4s;
    padding: 30px 0 40px;
  }  

  li:nth-child(5) {   
    visibility: visible;
    padding: 26.7px 0;
    text-align: center;
    a {
      color: $text-color;
      font-weight: bold;
      font-family:Georgia, 'Times New Roman', Times, serif;
      font-size: 12px;
    }
    button {
      padding:5px;
      background-color: $hover-menu;
      border-radius: 10px;
      &:hover {
        background-color: lightcoral;
        filter: drop-shadow(1px 1px 10px red);
      }
    }
  }
}
}

@media only screen and (max-width: 1000px) {
.home__name {
  li:nth-child(1) {   
    padding-top:40px;
  }

  li:nth-child(2) {
    animation: typing 2s steps(100, end) both;
    a {
      font-size: 45px;
      letter-spacing: 1px; 
      }
    }

  li:nth-child(3) {
    animation: typing 2s steps(100, end) both;
    animation-delay: 2s;
    font-size: 26px;
    letter-spacing: 1px; 
  }

  li:nth-child(4) {
    animation: typing 2s steps(100, end) both;
    animation-delay: 4s;
    font-size: 24px;
    letter-spacing: 1px; 
    padding: 30px 0 31.5px;
  } 

  li:nth-child(5) {   
    padding: 40.5px 0;
    a {
    font-size: 10px;
    letter-spacing: 1px; 
    }
    button {
    padding: 3px 4px 5px 4px;
    } 
  }  
}
}

@media only screen and (max-width: 700px) {

.home {
  margin: auto auto 20px;
}

.home__name {
  li:nth-child(1) {   
    padding-top:10px;
  }

  li:nth-child(2) {
    animation: typing 2s steps(100, end) both;
    margin: auto;
    a {
      font-size: 40px;
    }
  }

  li:nth-child(3) {
    animation: typing 2s steps(100, end) both;
    animation-delay: 2s;
    font-size: 22px;
  }  

  li:nth-child(4) {
    animation: typing 2s steps(100, end) both;
    animation-delay: 4s;
    font-size: 20px;
    padding: 30px 0 31.5px;
  } 

  li:nth-child(5) {   
    padding: 30px 0;
  }  
}
}








.thankyou {
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-right: -50%;
  transform: translate(-50%, -50%);
}

.thankyou h1, h2 {
  color: $text-color;
  padding: 20px 0;
}

.thankyou a {
